/**
 * Shadows
 */
/* --shadow-1: -4px 8px 24px hsla(240, 40%, 10%, 0.3);
--shadow-2: 0 16px 30px hsla(240, 40%, 10%, 0.25);
--shadow-3: 0 16px 40px hsla(240, 40%, 10%, 0.2);
